.Modal {
    .MuiPaper-root {
        max-width: none !important;
        max-height: none !important;
    }

    &--isResizable {
        .MuiPaper-root {
            max-height: fit-content !important;
            max-width: fit-content !important;
        }

        &-container {

        }

        &-handle {
            position: absolute;
            bottom: 0;
            right: 0;
            cursor: se-resize;
            width: 20px;
            height: 20px;
            background-repeat: no-repeat;
            background-origin: content-box;
            box-sizing: border-box;
            background-image: url(
                data:image/svg+xml;base64,
            );
            background-position: bottom right;
            padding: 0 3px 3px 0;
        }
    }

    &--isDraggable {
        &-handle {
            cursor: move;
        }
    }

    &--isCenter .MuiDialog-container {
        align-items: center !important;
        justify-content: center !important;
    }
    
    &--isTopCenter .MuiDialog-container {
        align-items: flex-start !important;
        justify-content: center !important;
    }

    &--isBottomCenter .MuiDialog-container {
        align-items: flex-end !important;
        justify-content: center !important;
    }

    &--isBottomLeft .MuiDialog-container {
        align-items: flex-end !important;
        justify-content: flex-start !important;
    }

    &--isTopLeft .MuiDialog-container {
        align-items: flex-start !important;
        justify-content: flex-start !important;
    }

    &--isBottomRight .MuiDialog-container {
        align-items: flex-end !important;
        justify-content: flex-end !important;
    }

    &--isTopRight .MuiDialog-container {
        align-items: flex-start !important;
        justify-content: flex-end !important;
    }
}